import React, { PureComponent } from "react";
import moment from 'moment'
import { AppUrl, ErrorUTM } from "../../../../constants";
import ErrorBranding from "../Elements/ErrorBranding";
import { onWindowEvent } from "../../../../utils";
export default class Error208 extends PureComponent {

  render() {
    const { older_days, older_post, editor } = this.props;
    return (
      <div className="tb_error_page_wrapper">
        {!editor ? <>
          <div className="tb_error_content">
            <div className="tb_error_title">No Posts!</div>
            <div className="tb_error_des">It appears that the available posts are older than your chosen
              {older_days !== 8 ? ` ${older_days} Days` : moment(new Date(older_post * 1000)).format('LL')}{'. '} All the new posts will start to appear here.</div>
            <div className="tb_error_page_actions">
              <div className="tb_error_action_list">
                <a href={`${AppUrl}${ErrorUTM}posts-days`} target={'_blank'} className="tb_error_btn_primary">Access Now</a>
              </div>
            </div>
          </div>
          <ErrorBranding utm_campaign={'posts-days'} /></> :
          <div className="tb_error_content">
            <div className="tb_error_title">No Posts!</div>
            <div className="tb_error_des">It appears that the available posts are older than your chosen
              {older_days !== 8 ? ` ${older_days} Days` : moment(new Date(older_post * 1000)).format('LL')}{'. '} All the new posts will start to appear here.</div>
            <div className="tb_error_page_actions">
              <div className="tb_error_action_list">
                <button onClick={() => onWindowEvent(2)} className="tb_error_btn_primary" style={{ marginRight: '10px' }}>Add Another Feed</button>
                <button onClick={() => onWindowEvent(1)} className="loadDemoWall tb_error_btn_secondary">Show Demo Posts</button>
              </div>
            </div>
          </div>}
      </div>
    );
  }
}

